body {
  background-color: black;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  width: 500px;
  background-color: black;
  color: white;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
}

.screen {
  grid-column: 1/5;
  height: 100px;
  text-align: right;
  font-size: 70px;
  padding-right: 20px;
  background-color: #595959;
  color: white;
}

.lightgray {
  background-color: #f2f2f2df;
  height: 100px;
  font-size: 45px;
}

.orange {
  background-color: #e37821;
  height: 100px;
  font-size: 45px;
  color: white;
}

#zero {
  grid-column: 1/3;
}



